<template>
    <div id="detail">
        <div v-if="charttype">
            <el-row class="row" :gutter="12">
                <el-col class="col">
                    <el-card shadow="always">
                        <pieCharts :op="pieoption1"></pieCharts>
                    </el-card>
                </el-col>
                <el-col class="coltips">
                    <el-card class="tipheight" shadow="always">
                        <div class="tips">说明:</div>
                        <p class="tippoint">根据教育部2010年《中等职业学校设置标准(教职成[2010]12号)》文件要求，中职学校占地面积<span style="color:red;">不少于40000m²</span>。</p>
                    </el-card>
                </el-col>
            </el-row>
        </div>
        <div v-else>
            <el-tabs v-model="activeName" @tab-click="handleClick" v-if="barData.dataCount2">
                <el-tab-pane label="各专业在校生数量" name="first" lazy>
                    <el-row class="row" :gutter="12">
                        <el-col class="col">
                            <el-card shadow="always">
                                <barCharts :op="baroption1"></barCharts>
                            </el-card>
                        </el-col>
                        <el-col class="coltips">
                            <el-card class="tipheight" shadow="always">
                                <div class="tips">说明:</div>
                                <p class="tippoint">根据教育部2010年《中等职业学校设置标准(教职成[2010]12号)》文件要求，中职学校占地面积<span style="color:red;">不少于40000m²</span>。</p>
                            </el-card>
                        </el-col>
                    </el-row>
                </el-tab-pane>
                <el-tab-pane label="学历教育在校生数量同比图" name="second" lazy>
                    <el-row class="row" :gutter="12">
                        <el-col class="col">
                            <el-card shadow="always">
                                <barCharts :op="baroption2"></barCharts>
                            </el-card>
                        </el-col>
                        <el-col class="coltips">
                            <el-card class="tipheight" shadow="always">
                                <div class="tips">说明:</div>
                                <p class="tippoint">根据教育部2010年《中等职业学校设置标准(教职成[2010]12号)》文件要求，中职学校占地面积<span style="color:red;">不少于40000m²</span>。</p>
                            </el-card>
                        </el-col>
                    </el-row>
                </el-tab-pane>
            </el-tabs>
            <el-row class="row" :gutter="12" v-else>
                <el-col class="col">
                    <el-card shadow="always">
                        <barCharts :op="baroption1"></barCharts>
                    </el-card>
                </el-col>
                <el-col class="coltips">
                    <el-card class="tipheight" shadow="always">
                        <div class="tips">说明:</div>
                        <p class="tippoint">根据教育部2010年《中等职业学校设置标准(教职成[2010]12号)》文件要求，中职学校占地面积<span style="color:red;">不少于40000m²</span>。</p>
                    </el-card>
                </el-col>
            </el-row>
        </div>
    </div>
</template>

<script>
import barCharts from './barCharts'
import pieCharts from './Pie'
export default {
    name: 'detail',
    data(){
        return{
            activeName: 'first',
            barData:null,
            charttype:'',
            xAxisData:[],
            seriesData:[],
            pielegend:[],
            pieseries:[],
            baroption1:{
                id:'charts1',
                dm:[],
                param:[],
                title:'',
                xAxis: [
                    {
                        data: null,
                        axisLabel: {
                            rotate:-40,
                            fontSize: 10
                        }
                    }
                ],
                yAxis: [
                    {
                        name:'(人)',
                    }
                ],
                series: [{
                    name: '人数',
                    data: null,
                }]
            },
            baroption2:{
                id:'charts2',
                title:'',
                param:[],
                xAxis: [
                    {
                        data: null,
                        axisLabel: {
                            rotate:0,
                        }
                    }
                ],
                yAxis: [
                    {
                        name:'(人)',
                    }
                ],
                series: [{
                    name: '人数',
                    data: null,
                }]
            },
            option:{
                color: ['#029348'],
                tooltip: {
                    trigger: 'axis',
                    axisPointer: {            // 坐标轴指示器，坐标轴触发有效
                        type: 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
                    }
                },
                xAxis: [
                    {
                        type : 'category',
                        data: [],
                        axisTick: {
                            // alignWithLabel: true,
                        },
                        axisLabel: {
                            interval:0,
                            rotate:0,
                            fontSize: 12
                        }
                    }
                ],
                yAxis: [
                    {
                        name:'',
                        type : 'value'
                    }
                ],
                series: [{
                    name: '',
                    type: 'bar',
                    data: [],
                    itemStyle: {
                        normal: {
                            label: {
                                show: true, //开启显示
                                position: 'top', //在上方显示
                                textStyle: { //数值样式
                                    color: '#029348',
                                    fontSize: 11
                                }
                            }
                        }
                    },
                }]
            },
            pieoption1:{
                id: 'chart1',
                title:'',
                param:[],
                tooltip : {
                    formatter: "{a} <br/>{b} : {c} ({d}%)"
                },
                legend: {
                    // data: ['图书馆','操场','其它','学生宿舍','教室']
                },
                color:['rgb(80,80,80)','rgb(124,179,73)','rgb(118,131,149)','rgb(53,143,226)','rgb(228,39,57)','rgb(128,146,149)','rgb(239,134,41)','rgb(240,240,240)',],
                series : [
                    {
                        // name: '固定资产',
                        data:[
                            {value:'', name:''},
                            {value:'', name:''},
                            {value:'', name:''},
                        ],
                        itemStyle: {
                            normal:{
                                label:{
                                    formatter: "{b} :\n{c} \n({d}%)",
                                }
                            },
                        }
                    }
                ]
            },
        }
    },
    components:{
        barCharts,
        pieCharts
    },
    methods:{
        handleClick(tab, event) {
            // console.log(tab, event);
        }
    },
    created() {
        console.log(this.$route.params)
        this.barData = this.$route.params
        var data = this.barData
        if(data.type == 'bt'){
            this.charttype = data.type
            for(var i = 0;i < data.dataCount1.length;i++){
                this.pielegend.push(data.dataCount1[i].mc)
                // this.pieseries.push(data.dataCount1[i].zcount)
                // this.pieoption1.series[0].data[i].value = this.pieseries
                // this.pieoption1.series[0].data[i].name = this.pielegend
                this.pieoption1.series[0].data[i].value = data.dataCount1[i].zcount
                this.pieoption1.series[0].data[i].name = data.dataCount1[i].mc
            }
            this.pieoption1.param.push(data.param)
            this.pieoption1.title = data.title
            this.pieoption1.series[0].name = '人数'
            this.pieoption1.legend = this.pielegend
            console.log(this.pieoption1.series[0])
        }
        if(data.dataCount1){
            this.xAxisData = []
            this.seriesData = []
            this.baroption1.title = data.title
            this.baroption1.param.push(data.param)
            for(var i = 0;i < data.dataCount1.length;i++){
                if(!data.dataCount1[i].mc){
                    data.dataCount1[i].mc = '其它'
                }
                this.xAxisData.push(data.dataCount1[i].mc)
                this.seriesData.push(data.dataCount1[i].zcount)
                this.baroption1.dm.push(data.dataCount1[i].dm)
            }
            if(data.rotate){
                this.baroption1.xAxis[0].axisLabel.rotate = data.rotate
            }
            // this.baroption1.yAxis[0].name = '㎡'
            this.baroption1.xAxis[0].data = this.xAxisData;
            this.baroption1.series[0].data = this.seriesData;
        }
        if(data.dataCount2){
            this.xAxisData = []
            this.seriesData = []
            this.baroption2.title = data.dataCount2[0].title
            this.baroption2.param.push(data.dataCount2[0].bl1)
            for(var i = 1;i < data.dataCount2.length;i++){
                this.xAxisData.push(data.dataCount2[i].mc)
                this.seriesData.push(data.dataCount2[i].zcount)
                // this.baroption1.dm.push(data.dataCount2[i].dm)
            }/*  */
            this.baroption2.xAxis[0].data = this.xAxisData;
            this.baroption2.series[0].data = this.seriesData;
        }
        
        // console.log(this.xAxisData,this.seriesData,this.baroption1.dm)
    },
    mounted() {
        
    },
}
</script>

<style>
#detail{
    width: 100%;
    height: 100%;
}
#detail .row{
    display:flex;
    justify-content:center;
}
#detail .row .col{
    width:750px;
    margin-top:20px;
}
#detail .cardtitle{
    font-size:25px;
    text-align:center;
    font-weight:900;
    margin-top:-20px;
    margin-bottom:-20px;
}
#detail .row .coltips{
    min-width:280px;
    max-width:300px;
    margin-top:20px;
}
#detail .row .coltips2{
    min-width:150px;
    max-width:300px;
    margin-top:20px;
}
#detail .shaixuan_box{
    position:relative;
    left:13%;
    bottom:32px;
}
@media all and (max-width: 1200px) {
    #detail .shaixuan_box{
        left: 3%!important;
    }
}
#detail .row .tipheight{
    height:435px;
}
#detail .row .tip_select{
    height:465px;
}
#detail .row .tips{
    font-size:25px;
    text-align:left;
    font-weight:900;
    margin-top:-20px;
    margin-bottom:-20px;
    padding:0 20px;
}
#detail .row .tippoint{
    margin-top:50px;
    padding:0 20px;
    line-height:40px;
}
#detail .el-tabs__item.is-active{
    color: #029348 !important;
}
#detail .el-tabs__active-bar{
    color: #029348 !important;
    background: #029348 !important;
}
#detail .is-top :hover{
    color: #029348 !important;
}
#detail .el-card{
    padding: 40px 0 40px 0;
}
#detail .el-tabs__content{
    overflow:inherit;
    padding-bottom: 20px;
}
#detail .el-card{
    transition: 0s;
}
</style>



